#services {
    margin: 80px 0;
    padding: 80px 0;
    position: relative;
  }
  .bgback {
    width: 100%;
    height: 50%;
    background: #64bcf4;
    position: absolute;
    left: 0;
    z-index: -10;
  }
  #services::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url(../../assets/imgs/shapetop.png);
    background-position: top;
    background-size: 100%;
    background-repeat: no-repeat;
    z-index: -2;
  }
  #services::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url(../../assets/imgs/shapedown.png);
    background-position: bottom;
    background-size: 100%;
    background-repeat: no-repeat;
    z-index: -2;
  }
  .servicescontainer {
    padding: 0 120px;
  }
  .seflex {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
    z-index: 10;
  }
  



  
  .backtitle {
    margin-bottom: 35px;
    text-align: center;
  }
  .backtitle h2 {
    color: #fff;
    font-family: "Circular Std";
    font-size: 40px;
  }
  #services .bg-holder {
    position: absolute;
    width: 100%;
    min-height: 100%;
    top: 0;
    left: 0;
    background-size: contain;
    background-position: center;
    overflow: hidden;
    will-change: transform, opacity, filter;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    background-repeat: no-repeat;
    z-index: -1;
    filter: brightness(10);
    background-size: 90% !important;
    opacity: 0.1;
  }

@media only screen and (max-width:900px) {
  .servicescontainer {
    padding: 0 20px;
  }
  .bgback {
    height: 95%;
    top: 10px;
  }
}