#request {
    margin: 30px 0 20px 0;
    padding: 30px 120px;
  }
  .requestcontainer {
    background: #64bcf4;
    padding: 35px 55px;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .requestleft h2 {
    color: #fff;
    font-size: 18px;
    line-height: 30px;
    font-family: 'Varela Round', sans-serif;
    text-align: left;
    width: 60%;
  }
  .requestright {
    flex: none;
  }
  .requestright a {
    background: #fff;
    padding: 14px 45px;
    border-radius: 30px;
    font-size: 15px;
    font-family: 'Varela Round', sans-serif;
    color: #64bcf4;
    font-weight: 600;
    opacity: 0.9;
    transition: 0.3s;
  }
  .requestright a:hover {
    opacity: 1;
}

  
@media only screen and (max-width:720px) {
  #request {
    padding: 30px 20px;
  }
  .requestcontainer {
    display: block;
    padding: 35px;
  }
  .requestleft h2 {
    width: 100%;
    text-align: center;
  }
  .requestleft {
    margin-bottom: 20px;
  }
  .requestright a {
    margin-top: 25px;
    width: 100%;
    padding: 14px 0;
    text-align: center;
    display: block;
  }
}

