#portfolio {
    margin-top: 60px;
    min-height: 90vh;
}
#portfolio .title .main-title {
  margin-top: -17%;
}
.portflex {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.portfoliocontainer {
  padding: 30px 120px;
}
.portgallery{
  --bs-gutter-x: 1.5rem;
  --bs-gutter-y: 0;
  display: flex;
  flex-wrap: wrap;
  margin-top: calc(-1 * var(--bs-gutter-y));
  margin-right: calc(-.5 * var(--bs-gutter-x));
  margin-left: calc(-.5 * var(--bs-gutter-x));
}

.backbtn {
  width: 100%;
  text-align: right;
}

@media only screen and (max-width:720px) {
  .portfoliocontainer {
    padding: 50px 20px;
  }
  .portgallery {
    display: block;
  }
  .griditem {
    width: 100%;
  }
}