.flexitem {
    flex: 0 0 auto;
    width: 25%;
  }
  .seitem {
    padding: 30px;
    margin: 10px;
    border-radius: 10px;
    background: #fff;
    display: grid;
    transition: all 0.35s ease;
    cursor: pointer;
  }
  .itemicon {
    line-height: 1.3;
    position: relative;
    margin-top: -60px;
    width: 80px;
    height: 80px;
    background: #4f97c5;
    border-radius: 50%;
    margin-bottom: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.35s ease;
    box-shadow: 0 5px 12px 0 rgb(0,0,0,0.1);
  }
  .itemicon svg {
    color: #fff;
    font-size: 28px;
  }
  .itembody {
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
  }
  .itembody h3 {
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 1rem;
    line-height: 1.5;
  }
  .itembody p {
    margin-top: 0;
    margin-bottom: 1rem;
    color: #9f9f9f;
  }
  .seitem:hover {
    background: #4f97c5;
  }
  .seitem:hover h3 {
    color: #fff;
  }
  .seitem:hover p {
    color: #b2e1ff;
  }
  .seitem:hover .itemicon {
    background: #fff;
    transform: translateX(15px);
  }
  .seitem:hover .itemicon svg {
    color: #4f97c5;
  }

@media only screen and (max-width:900px) {
  .flexitem {
    flex: 0 0 auto;
    width: 100%;
    margin-top: 50px;
  }
}