.footerbx ul li {
    list-style: none;
    position: relative;
    margin: 0 0 12px;
  }
  .footerbx ul li a {
    font-size: 14px;
    line-height: 26px;
    color: #959595;
    position: relative;
    padding-left: 34px;
    transition: 0.3s;
    display: flex;
    align-items: center;
  }
.footerbx ul li a svg {
    font-size: 14px;
    color: inherit;
    line-height: 50px;
    position: absolute;
    left: -2px;
    top: 6px;
}
  .footerbx ul li a:hover {
    color: #64bcf4;
    padding-left: 29px;
  }