#resume {
    margin-top: 60px;
    min-height: 90vh;
  }
  .resumecontent {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .resumecontent .left {
    width: 50%;
    padding: 0 10px;
  }
  .resumecontent .right {
    width: 50%;
    padding: 0 10px;
  }
  .resumecontainer {
    padding: 0 120px;
  }
  .exp {
    margin: 15px;
    padding: 25px 40px 20px 20px;
  }
  .timeline {
    padding: 30px 0 15px 0;
    width: 100%;
    position: relative;
  }
  .timeline-item {
    position: relative;
    padding-left: 37px;
    padding-bottom: 40px;
  }
  .timeline .timeline-item:last-child{
    padding-bottom: 0;
  }
  .timeline .timeline-item::before {
    content: '';
    width: 1px;
    position: absolute;
    height: 100%;
    left: 7px;
    top: 5px;
    background-color: #64bcf4;
  }
  .circle-dot {
    position: absolute;
    left: 0;
    top: 2px;
    height: 15px;
    width: 15px;
    border-radius: 50%;
    background-color: #64bcf42e;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .circledot {
    background-color: #64bcf4;
    height: 65%;
    width: 65%;
    border-radius: 50%;
  }
  .timeline .timeline-date {
    font-weight: 400;
    font-size: 14px;
    margin-bottom: 20px;
    color: #6e8493;
  }
  .timeline .timeline-date .fa {
    margin-right: 5px;
  }
  .timeline .timeline-title {
    font-family: 'Spartan', sans-serif;
    font-weight: 700;
    font-size: 18px;
    margin-bottom: 12px;
    text-transform: capitalize;
    color: #000;
  }
  .timeline .timeline-text {
    font-family: 'Spartan', sans-serif;
    line-height: 25px;
    font-size: 14px;
    text-align: justify;
    font-weight: 600;
    color: #35586e;
  }
  .exp h2 {
    font-family: 'Spartan', sans-serif;
    font-size: 25px;
    font-weight: 800;
  }
  .skills{
    width: 100%;
    margin-top: 35px;
  }
  .skill-name {
    font-size: 15px;
    font-weight: 700;
    color: #64bcf4;
    margin-bottom: 8px;
    font-family: 'Spartan';
  }
  .skill {
    margin-bottom: 35px;
  }
  .skill-bar {
    height: 8px;
    background: #bfd0db;
    border-radius: 3px;
  }
  .skill-per {
    height: 8px;
    background: #64bcf4;
    border-radius: 3px;
    position: relative;
    animation: fillBars 2.5s 1;
    color: #fff;
    font-size: 12px;
  }
  .skill-per::before {
    content: attr(per);
    position: absolute;
    padding: 4px 8px;
    background: #64bcf4;
    border-radius: 4px;
    font-size: 10px;
    top: -32px;
    right: 0;
    transform: translateX(50%);
  }
  .skill-per::after {
    content: "";
    position: absolute;
    width: 8px;
    height: 8px;
    background: #64bcf4;
    top: -13px;
    right: 0;
    transform: translateX(50%) rotate(45deg);
    border-radius: 2px;
  }
  @keyframes fillBars{
    from{
      width: 0;
    }
    to{
      width: 100%;
    }
  }

@media only screen and (max-width:900px) {
  .resumecontainer {
    padding: 0 20px;
  }
  .resumecontent {
    display: block;
  }
  .resumecontent .left,
  .resumecontent .right {
    width: 100%;
    padding: 0;
  }
  .exp h2 {
    text-align: center;
  }
}