.herocontainer {
  position: relative;
  max-width: 81rem;
  width: 100%;
  margin: 0 auto;
  padding: 0 3rem;
  z-index: 10;
}
nav {
  position: fixed;
  z-index: 70;
  width: 100%;
  left: 0;
  transition: 0.5s;
  height: 90px;
  display: flex;
}
nav.sticky {
  background-color: rgb(255 255 255 / 23%);
  backdrop-filter: blur(4px);
  top: 0;
  height: 70px;
  display: flex;
  box-shadow: 0 5px 10px 0 rgb(0 0 0 / 10%);
}
nav .herocontainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.overlay {
  display: none;
}
.logo {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.logo img {
  width: 40px;
  margin-right: 0.6rem;
  margin-top: -0.6rem;
}
.logo h3 {
  font-family: 'Spartan', sans-serif !important;
  color: #64bcf4;
  font-size: 1.55rem;
  line-height: 1.2;
  font-weight: 800;
}
.links ul {
  display: flex;
  list-style: none;
  align-items: center;
}
.links a, .links p {
  color: #aaa;
  margin-left: 3rem;
  display: inline-block;
  transition: 0.3s;
  cursor: pointer;
}
.links a:hover, .links p:hover {
  color: #5bacdf;
  transform: scale(1.05);
}
.btn {
  display: inline-block;
  padding: 10px 30px;
  color: #fff !important;
  background-color: #64bcf4;
  border-radius: 30px;
  text-transform: capitalize;
  transition: 0.3s !important;
  font-size: 15px;
  font-weight: 500;
}
.btn:hover {
  background-color: #5bacdf;
  transform: scale(1.02) !important;
}
.hamburger-menu {
  position: relative;
  z-index: 99;
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  display: none;
}
.hamburger-menu .bar {
  position: relative;
  width: 100%;
  height: 3px;
  background-color: #64bcf4;
  border-radius: 3px;
  transition: 0.5s;
}
.bar::before,
.bar::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #64bcf4;
  border-radius: 3px;
  transition: 0.5s;
}
.bar::before {
  transform: translateY(-8px);
}
.bar::after {
  transform: translateY(8px);
}
header.active .hamburger-menu .bar {
  background-color: transparent;
}

header.active .bar::before {
  background: #fff;
  transform: translateY(0) rotate(-45deg);
}
  
header.active .bar::after {
  background: #fff;
  transform: translateY(0) rotate(45deg);
}
nav.active .hamburger-menu .bar {
  background-color: transparent;
}
nav.active .bar::before {
  transform: translateY(0) rotate(-45deg);
}
nav.active .bar::after {
  transform: translateY(0) rotate(45deg);
}


@media screen and (max-width: 900px) {
  .hamburger-menu {
    display: flex;
  }
  .links {
    position: fixed;
    top: 0;
    right: 0;
    max-width: 450px;
    width: 100%;
    height: 100%;
    background-color: #64bcf4;
    z-index: 95;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translateX(100%);
    transition: 0.5s;
  }
  .links ul {
    flex-direction: column;
  }
  .links a, .links p {
    color: #fff;
    margin-left: 0;
    padding: 2rem 0;
  }
  .links .btn {
    background: none;
  }
  .overlay {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
  }
  nav.active .links {
    transform: translateX(0);
    box-shadow: 0 0 50px 2px rgba(0, 0, 0, 0.4);
  }
  nav.active .overlay {
    pointer-events: all;
    opacity: 1;
  }
  .logo h3 {
    font-size: 1.25rem;
  }
}