.btitle {
    display: flex;
    align-items: center;
    justify-content: center;
}
.title {
    text-transform: uppercase;
    transform: rotate(355deg);
}
.title .title-bg {
    font-size: 60px;
    font-weight: 900;
    font-style: italic;
    -webkit-text-stroke-width: 0.8px;
    -webkit-text-stroke-color: #64bcf4;
    color: transparent;
}
.title .main-title {
    font-size: 40px;
    font-weight: 900;
    font-style: italic;
    margin-top: -23%;
    text-align: center;
    color: #4e92bd;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: #f1f8fc;
}