#contact {
    background: #64bcf4;
    position: relative;
    overflow: hidden;
  }
  #contact::before {
    content: '';
    position: absolute;
    right: -65px;
    top: -65px;
    width: 180px;
    height: 180px;
    background: #a5dcff;
    border-radius: 50%;
    z-index: 0;
  }
  #contact::after {
    content: '';
    position: absolute;
    left: 655px;
    bottom: -212px;
    width: 300px;
    height: 300px;
    background: #a5dcff;
    border-radius: 50%;
    z-index: 0;
  }
  .cncontainer {
    padding: 35px 120px;
  }
  .cnflex {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .cnleft {
    width: 60%;
    z-index: 1;
  }
  .cnright {
    width: 40%;
    z-index: 1;
  }
  .cntitle {
    font-size: 2.3rem;
    font-weight: 700;
    line-height: 1;
    color: #fff;
    font-family: "Circular Std";
  }
  .topse p {
    font-family: "Circular Std";
    color: #c3e7fd;
    font-weight: 300;
    margin-top: 15px;
    width: 65%;
    margin-bottom: 20px;
  }
  .topse {
    margin-bottom: 60px;
  }
  .middlese {
    margin-bottom: 50px;
  }
  .btmse {
    margin-top: 20px;
  }
  .email,
  .phone,
  .insta {
    display: flex;
    margin-bottom: 10px;
  }
  .txt {
    display: flex;
    align-items: center;
    margin-left: 10px;
  }
  .mdicon {
    color: #fff;
    font-size: 16px;
  }
  .social {
    display: flex;
    align-items: center;
  }
  .sicon {
    background: #fff;
    height: 35px;
    width: 35px;
    border-radius: 6px;
    margin-right: 10px;
    box-shadow: 0 0 10px 0 rgb(0 0 0 / 10%);
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 1;
    transition: 0.3s;
  }
  .sicon:hover {
    opacity: 0.75;
  }
  .sicon svg {
    font-size: 15px;
    color: #487fa3;
  }
  .txt h5 {
    color: #fff;
    font-family: "Circular Std";
    font-size: 16px;
  }
  .contactus {
    margin: 10px;
    padding: 35px 25px;
    background: #fff;
    border-radius: 15px;
    box-shadow: 0 0 15px 0 rgba(0,0,0,0.15);
  }
  .inputgroup {
    position: relative;
    width: 100%;
    height: 2.3rem;
    margin-bottom: 24px;
  }
  .form__input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 1px solid #98bcd3;
    border-radius: 0.5rem;
    font-family: "Circular Std";
    font-size: 16px;
    color: #64bcf4;
    outline: none;
    padding: 1.25rem;
    background: none;
  }
  .form__input:hover {
    border-color: #7cc7f5;
  }
  .form__input:focus {
    border-color: #64bcf4;
  }
  .textarea {
    height: 130px;
  }
  textarea.form__input {
    resize: none;
    height: 100%;
  }
  .form__label {
    font-family: "Circular Std";
    font-size: 16px;
    position: absolute;
    left: 1rem;
    top: 12px;
    padding: 0 0.5rem;
    color: #98bcd3;
    cursor: text;
    transition: top 200ms ease-in, left 200ms ease-in, font-size 200ms ease-in;
    background-color: #fff;
  }
  .form__input:focus ~ .form__label,
  .form__input:not(:placeholder-shown).form__input:not(:focus) ~ .form__label {
    top: -0.5rem;
    font-size: 14px;
    left: 0.8rem;
    color: #64bcf4;
  }
  .submitbtn {
    background: #64bcf4;
    border: none;
    color: #fff;
    font-family: "Circular Std";
    font-size: 16px;
    padding: 10px 35px;
    border-radius: 0.5rem;
    cursor: pointer;
  }
  .alert {
    font-family: "Circular Std";
    font-size: 14px;
    color: #ff4757;
    margin-bottom: 7px;
    margin-left: 15px;
    transition: 0.3s;
  }
  .alert.active {
    margin-top: -10px;
  }
  .last {
    margin-bottom: 15px;
}
@media screen and (max-width: 720px) {
  .herocontainer {
    padding: 0 1.5rem;
  }
  .big-title {
    font-size: 0.9rem;
  }
  .text {
    margin: 1.1rem 0 1.5rem;
  }
  .showcase-area .btn {
    font-size: 0.8rem;
  }
  .cnflex {
    display: block !important;
  }
  .cnleft, .cnright {
    width: 100% !important;
  }
  .cnleft {
    margin-bottom: 50px;
  }
  .cncontainer {
    padding: 35px 65px !important;
  }
  .cntitle, .topse p {
    text-align: center;
    width: 100% !important;
  }
  .contactus {
    margin: 0 !important;
  }
}