#footer {
  position: relative;
  background: url(../../assets/imgs/footerback.png) no-repeat left top #f2f3fb;
}
.ftcontainer {
  padding: 85px 120px 30px 120px;
}
.footerflex {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.copyright {
  font-family: 'Spartan', sans-serif;
  position: relative;
  font-size: 11px;
  color: #6b6b6b;
  padding: 23px 0;
  text-align: center;
  margin-top: 40px;
}

@media only screen and (max-width:900px) {
  .ftcontainer {
    padding: 45px;
  }
  .footerflex {
    display: block;
  }
}