.hero {
  position: relative;
  padding: 1.7rem 0 2rem;
  padding-top: 10px;
  width: 100%;
  min-height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.shape {
  position: absolute;
  z-index: 0;
  width: 500px;
  bottom: -180px;
  left: -15px;
  opacity: 0.1;
}
.showcase-area .herocontainer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  justify-content: center;
}
.big-title {
  font-size: 1.4rem;
  color: #312f3a;
  text-transform: capitalize;
  line-height: 1.4;
}
.big-title h1 b {
  color: #64bcf4;
}
.text {
  color: #919191;
  font-size: 1.1rem;
  margin: 1.9rem 0 2.5rem;
  max-width: 600px;
  line-height: 2.3;
}
.showcase-area .btn {
  box-shadow: 0 0 40px 2px rgba(0, 0, 0, 0.05);
}
.person {
  width: 95%;
  transform: translate(40%, 20px);
}


@media screen and (max-width: 900px) {
  .showcase-area {
    padding: 2.5rem 0;
    max-width: 700px;
    margin: 0 auto;
    margin-top: 70px;
  }
  .showcase-area .herocontainer {
    grid-template-columns: 1fr;
    justify-content: center;
    grid-gap: 2rem;
  }
  .big-title {
    font-size: 1.1rem;
  }
  .text {
    font-size: 0.95rem;
    margin: 1.4rem 0 1.5rem;
  }
  .person {
    width: 100%;
    transform: none;
  }
  .shape {
    bottom: -180px;
    left: -150px;
  }
}

@media screen and (max-width: 720px) {
  .herocontainer {
    padding: 0 1.5rem;
  }
  .big-title {
    font-size: 0.9rem;
  }
  .text {
    margin: 1.1rem 0 1.5rem;
  }
  .showcase-area .btn {
    font-size: 0.8rem;
  }
}
@media only screen and (max-width: 500px){
  .shape {
    bottom: -215px;
  }
}