#testimonials {
    position: relative;
    margin: 80px 0;
    padding: 5rem 0;
  }
  #testimonials .bg-holder {
    position: absolute;
    width: 100%;
    min-height: 100%;
    top: 0;
    left: 0;
    background-size: cover;
    background-position: center;
    overflow: hidden;
    will-change: transform, opacity, filter;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    background-repeat: no-repeat;
    z-index: 0;
  }
  .testcontainer {
    padding: 20px 200px;
  }
  .titlerow {
    --bs-gutter-x: 2rem;
    --bs-gutter-y: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-top: calc(var(--bs-gutter-y) * -1);
    margin-right: calc(var(--bs-gutter-x) / -2);
    margin-left: calc(var(--bs-gutter-x) / -2);
    margin-bottom: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .testtitle {
    -webkit-box-flex: 0;
    -ms-flex: 0 0 auto;
    flex: 0 0 auto;
    width: auto;
    text-align: center;
  }
  .testtitle h1 {
    font-size: 3rem;
    font-weight: 700;
    line-height: 1;
    color: #272D4E;
    font-family: "Circular Std";
  }
  .testimonialrow {
    --bs-gutter-x: 2rem;
    --bs-gutter-y: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-top: calc(var(--bs-gutter-y) * -1);
    margin-right: calc(var(--bs-gutter-x) / -2);
    margin-left: calc(var(--bs-gutter-x) / -2);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .testimonialinner {
    -webkit-box-flex: 0;
    -ms-flex: 0 0 auto;
    flex: 0 0 auto;
    width: 58.33333%;
  }
  #slider {
    position: relative;
    margin: 0 auto;
    width: 800px;
    max-width: 100%;
    text-align: center;
  }
  #slider .bg-holder {
    position: absolute;
    width: 100%;
    min-height: 100%;
    top: 0;
    left: 0;
    background-size: cover;
    background-position: center;
    overflow: hidden;
    will-change: transform, opacity, filter;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    background-repeat: no-repeat;
    z-index: 0;
    margin-top: 1.8rem;
    height: 168px;
  }
  .slide-content {
    z-index: 1;
    -webkit-box-flex: 1;
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;
    padding: 1rem 1rem;
  }
  #slider input[type=radio] {
    display: none;
  }
  #slider label {
    cursor:pointer;
    text-decoration: none;
  }
  #slides {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    background-color: #fff;
    background-clip: border-box;
    border-radius: 1rem;
    box-shadow: 0 1.5625rem 2.8125rem rgb(111 121 149 / 8%);
    padding: 2rem 3rem;
  }
  #overflow {
    width: 100%;
    overflow: hidden;
  }
  #slide1:checked ~ #slides .inner {
    margin-left: 0;
  }
  #slide2:checked ~ #slides .inner {
    margin-left: -100%;
  }
  #slide3:checked ~ #slides .inner {
    margin-left: -200%;
  }
  #slide4:checked ~ #slides .inner {
    margin-left: -300%;
  }
  #slides .inner {
    transition: margin-left 800ms cubic-bezier(0.770, 0.000, 0.175, 1.000);
    width: 400%;
    line-height: 0;
    height: 300px;
  }
  #slides .slide {
    width: 25%;
    float:left;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    color: #fff;
  }
  #slides .slide {
    background: #fff;
  }
  #controls {
    width: 100%;
    height: 50px;
    z-index: 3;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: -25px;
  }
  .labels {
    padding: 10px 15px;
    background: #fff;
    border-radius: 25px;
    box-shadow: 0 0 20px 0 rgb(0 0 0 / 10%);
    width: 15%;
  }
  #controls label {
    transition: opacity 0.2s ease-out;
    display: none;
    width: 25px;
    height: 25px;
    opacity: .4;
  }
  #controls label:hover {
    opacity: 1;
  }
  #slide1:checked ~ #controls label:nth-child(2),
  #slide2:checked ~ #controls label:nth-child(3),
  #slide3:checked ~ #controls label:nth-child(4),
  #slide4:checked ~ #controls label:nth-child(1) {
    background: url(https://cdn-icons-png.flaticon.com/512/271/271228.png) no-repeat;
    float:right;
    display: block;
    background-size: 65%;
    background-position: center;
  }
  #slide1:checked ~ #controls label:nth-last-child(2),
  #slide2:checked ~ #controls label:nth-last-child(3),
  #slide3:checked ~ #controls label:nth-last-child(4),
  #slide4:checked ~ #controls label:nth-last-child(1) {
    background: url(https://cdn-icons-png.flaticon.com/512/271/271220.png) no-repeat;
    float:left;
    display: block;
    background-size: 65%;
    background-position: center;
  }
  #slide1:checked ~ #bullets label:nth-child(1),
  #slide2:checked ~ #bullets label:nth-child(2),
  #slide3:checked ~ #bullets label:nth-child(3),
  #slide4:checked ~ #bullets label:nth-child(4) {
    background: #444;
  }
  .slide-content p {
    color: #94A2B3;
    font-weight: 400;
    font-size: 1.44rem;
    line-height: 1.5;
    font-family: "Circular Std";
  }
  

@media screen and (max-width: 900px) {
  #slide1:checked ~ #controls label:nth-child(2),
  #slide2:checked ~ #controls label:nth-child(3),
  #slide3:checked ~ #controls label:nth-child(4),
  #slide4:checked ~ #controls label:nth-child(1),
  #slide1:checked ~ #controls label:nth-last-child(2),
  #slide2:checked ~ #controls label:nth-last-child(3),
  #slide3:checked ~ #controls label:nth-last-child(4),
  #slide4:checked ~ #controls label:nth-last-child(1) {
    margin: 0;
  }
  .testtitle h1 {
    font-size: 2.3rem;
  }
  .testcontainer {
    padding: 20px;
  }
  #slides {
    max-width: calc(100% - 50px);
    margin: 0 auto;
    width: 100%;
    padding: 3.5rem 1.8rem;
  }
  .testimonialinner {
    width: 100%;
  }
  .labels {
    width: 22%;
  }
  #slide1:checked ~ #controls label:nth-last-child(2), 
  #slide2:checked ~ #controls label:nth-last-child(3), 
  #slide3:checked ~ #controls label:nth-last-child(4), 
  #slide4:checked ~ #controls label:nth-last-child(1) {
    background-size: 50%;
  }
  #slide1:checked ~ #controls label:nth-child(2), 
  #slide2:checked ~ #controls label:nth-child(3), 
  #slide3:checked ~ #controls label:nth-child(4), 
  #slide4:checked ~ #controls label:nth-child(1) {
    background-size: 50%;
  }
  .slide-content p {
    font-size: 1.25rem;
  }
}

