#newsletter {
    background: url(../../assets/imgs/newsletter.png);
    padding: 40px 80px;
  }
  .inf p {
    width: 60%;
    font-family: 'Spartan', sans-serif;
    line-height: 25px;
    font-size: 14px;
    font-weight: 500;
    color: #b2e8ff;
  }
  .inf h2 {
    font-family: 'Spartan', sans-serif;
    color: #fff;
    margin-bottom: 10px;
    font-weight: 700;
  }
  .nlflex {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .nlflex form {
    position: relative;
    margin: 0;
    padding: 0;
    border-radius: 5px;
    overflow: hidden;
    outline: none;
  }
  .nlflex input {
    width: 400px;
    height: 55px;
    padding: 0 30px;
    color: #938eae;
    margin: 0;
    border: none;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    transition: 0.3s;
    outline: none;
  }
  .nlflex button {
    position: absolute;
    right: 0;
    top: 0;
    height: 55px;
    background: #3abff9;
    color: #fff;
    font-size: 14px;
    border: none;
    border-radius: 0;
    min-width: 131px;
    padding: 0 15px;
    text-align: center;
    transition: all ease 300ms;
    -moz-transition: all ease 300ms;
    -webkit-transition: all ease 300ms;
    -o-transition: all ease 300ms;
    -ms-transition: all ease 300ms;
    cursor: pointer;
}
@media only screen and (max-width:720px) {
  .nlflex {
    display: block;
  }
  #newsletter {
    padding: 40px;
  }
  .inf {
    text-align: center;
  }
  .inf p {
    width: 100%;
    margin-top: 10px;
  }
  .nlflex form {
    margin-top: 45px;
    box-shadow: 0 0 30px 0 rgb(0 0 0 / 20%);
  }
}