.griditem {
    flex: 0 0 auto;
    width: 33.33333333%;
}
figure {
    cursor: pointer;
    margin: 0 0 1rem;
}
.insgrid {
    padding: 10px;
    display: block;
}
.griditem figure {
    position: relative;
    overflow: hidden;
    border-radius: 10px;
}
.griditem figure img {
    position: relative;
    width: 100%;
    display: block;
}
.griditem figure figcaption {
    position: absolute;
    padding: 15px;
    height: 100%;
    width: 100%;
    top: auto;
    left: 0;
    color: #fff;
    bottom: 0;
    z-index: 10;
    transition: 0.5s;
    font-family: 'Spartan', sans-serif;
}
.griditem figure figcaption::after {
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    transition: all 0.4s ease-out;
    transform: translateX(0) translateY(0) rotateZ(0) rotate(0deg) scale(0.95);
    z-index: -1;
    opacity: 0;
}
.griditem figure figcaption svg {
    font-size: 28px;
    line-height: 35px;
    position: absolute;
    text-align: center;
    width: 100%;
    top: 48%;
    left: 0;
    margin-top: -22px;
    transform: translateX(0) translateY(0) translateZ(0) rotate(0deg) scale(1);
    opacity: 0;
    letter-spacing: none;
}
.figtitle {
    position: absolute;
    font-family: 'Spartan', sans-serif;
    width: 100%;
    left: 0;
    right: 0;
    font-size: 18px;
    line-height: 30px;
    text-transform: capitalize;
    top: 50%;
    margin-top: 30px;
    transform: translateX(80px) translateY(0) translateZ(0) rotate(0deg) scale(1);
    transition: all 0.4s ease-out;
    opacity: 0;
    letter-spacing: none;
    text-align: center;
}
.figsubtitle {
    font-family: 'Spartan', sans-serif;
    position: absolute;
    text-align: center;
    width: 100%;
    left: 0;
    font-size: 13px;
    top: 52%;
    margin-top: 30px;
    transform: translateX(-70px) translateY(0) translateZ(0) rotate(0deg) scale(1);
    transition: 0.5s;
    opacity: 0;
    letter-spacing: none;
    color: #e9e9e9;
}
.griditem figure:hover {
    transform: all 0.4s ease-out;
}
figure:hover figcaption {
    color: #fff;
    background: rgba(100, 188, 244, 0.8);
}
.griditem figure:hover figcaption::after {
    opacity: 0;
    transform: translateX(0) translateY(0) rotateZ(0) rotate(0deg) scale(1);
}
.griditem figure:hover figcaption svg {
    transform: translateX(0) translateY(0) rotateZ(0) rotate(0deg) scale(1);
    transition: 0.2s;
    opacity: 1;
}
.griditem figure:hover figcaption .figtitle {
    transform: translateX(0) translateY(0) rotateZ(0) rotate(0deg) scale(1);
    transition: 0.4s;
    margin-top: 8px;
    opacity: 1;
}
.griditem figure:hover figcaption .figsubtitle {
    transform: translateX(0) translateY(0) rotateZ(0) rotate(0deg) scale(1);
    transition: 0.6s;
    margin-top: 35px;
    opacity: 1;
}