.footerbx {
    width: 100%;
    display: grid;
    align-items: center;
    justify-content: center;
  }
  .footerbx .bxtitle {
    font-family: 'Spartan', sans-serif;
    font-size: 18px;
    color: #1e1e1e;
    line-height: .8;
    position: relative;
    padding: 0 0 17px;
    margin: 0 0 32px;
  }
  .footerbx .bxtitle:after {
    width: 50px;
    height: 2px;
    background: #64bcf4;
    position: absolute;
    left: 0;
    bottom: 0;
    content: '';
  }
  
@media only screen and (max-width:900px) {
  .footerbx {
    margin-top: 30px;
  }
  .footerbx .bxtitle:after {
    width: 100%;
  }
}