header {
    height: 90px;
}


@media screen and (max-width: 900px) {
    header.active nav.sticky {
        backdrop-filter: none;
    }
    header.active .links {
        transform: translateX(0);
        box-shadow: 0 0 50px 2px rgba(0, 0, 0, 0.4);
    }
    header.active .overlay {
        pointer-events: all;
        opacity: 1;
    }
}
