.errorpage {
    background: #64bcf4;
    padding: 100px 80px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.notfound {
    max-width: 520px;
    width: 100%;
    line-height: 1.4;
    text-align: center;
}
.notfound .notfound-404 {
    position: relative;
    height: 240px;
}
.notfound .notfound-404 h1 {
    position: relative;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    font-size: 252px;
    font-weight: 900;
    margin: 0px;
    color: #fff;
    text-transform: uppercase;
    letter-spacing: -40px;
    margin-left: -35px;
    width: max-content;
} 
.notfound .notfound-404 h1>span {
    text-shadow: -8px 0px 0px #64bcf4;
}
.notfound .notfound-404 h3 {
    font-size: 17px;
    color: #fff;
    margin: 0px;
    letter-spacing: 3px;
    padding-left: 6px;
}  
.notfound h2 {
    font-size: 20px;
    font-weight: 500;
    color: #fff;
    margin-top: 10px;
    margin-bottom: 25px;
}
@media only screen and (max-width:720px) {
    .notfound .notfound-404 h1 {
        font-size: 200px;
    }
}